import React, { Component } from 'react';
import './App.css';
import YouTube from 'react-youtube';
import YouTubePlayer from 'youtube-player';
import axios from 'axios';


/*
  Insert your own LastFM and YouTube API Keys here
*/
var youtubeKey = 'AIzaSyBhs_zuoG2Aqhs3kV_3GWodZnMlcTCYgXM';
var lastfmKey = 'dab036bc72f981c2ef22e80e69d56d5c';


class App extends Component {
  constructor(props) {
    super(props);

    var self = this;
    var url = 'http://ws.audioscrobbler.com/2.0/?method=tag.gettoptracks&tag=halloween&api_key='+lastfmKey+'&format=json&limit=250';

    this.state = {
      videoId: null,
      player: null,
      tracks: null,
      options: {}
    };

    //get list of halloween themed songs from lastfm
    axios.get(url).then(function(response){
      self.state.tracks = response.data.tracks.track;
      self.playRandomVideo();
    });

    this.onReady = this.onReady.bind(this);
    this.playRandomVideo = this.playRandomVideo.bind(this);
  }


  /*
    Play a random song
  */
  playRandomVideo() {
    var self = this;
    var track = this.state.tracks[Math.floor(Math.random()*this.state.tracks.length)];
    var url = 'https://www.googleapis.com/youtube/v3/search';
    var params = {
      key: youtubeKey,
      type: 'video',
      maxResults: '1',
      part: 'id,snippet',
      fields: 'items/id,items/snippet/title,items/snippet/thumbnails/default,items/snippet/channelTitle',
      q: track.name + ' - ' + track.artist.name
    }

    axios.get(url, {params: params}).then(function(response){
      self.setState({
        videoId: self.state.videoId = response.data.items[0].id.videoId,
        options: {
          playerVars: {
            autoplay: (self.state.options.playerVars ? 1 : 0)
          }
        }
      });
    })
  }

  onReady(event) {
    this.setState({
      player: event.target,
    });
  }

  render() {
    return (
      <div className="container">
        <div className="github">
          <a className="github-button" href="https://github.com/andypotts/halloween-music" data-size="large" data-show-count="true" aria-label="Star andypotts/halloween-music on GitHub">Star</a>
        </div>
        <div className="content">
          <h1 className="title">Halloween Music Player</h1>
          <YouTube
            id="player"
            opts={this.state.options}
            videoId={this.state.videoId}
            onReady={this.onReady}
            onEnd={this.playRandomVideo}
          />
          <a href="#" className="shuffle-button" onClick={this.playRandomVideo}>Random Song</a>
        </div>
        <a href="https://unsplash.com/photos/ZK1HZiMZ2EM" target="_blank" title="Photo by John Silliman" rel="nofollow" className="photo-attribution">Photo by John Silliman</a>
      </div>
    );
  }
}

export default App;



